<template lang="pug">
  .call-component-container
    .call-form-container
      form#call-form.form-call( @submit='sendCall()')
        label Tú número
        input#from.number-input(type='number' v-model="from")
        label Número de Contacto
        input#to.number-input(type='number' v-model="to")
        input.go-input(type='submit' value='Call') 
</template>


<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      from: '',
      to:''
    }
  },
  methods:{
    sendCall(){
      var from = `521${this.from}` ;
      var to = `521${this.to}` ;

      console.log(`Calling from: ${from} to: ${to}`);

      this.$http.get(`https://proxy-secure-phone.herokuapp.com/conversation/start/${from}/${to}`).then((res) => {
        console.log(res);
        if(res.status == 200 && from && to){
          window.location.href= `tel:${res.body.number}`;
        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.call-form-container{
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: center;
  font-size: 18px;
  color: #424242;
  padding: 20px;
}

.form-call{
  display: flex;
  flex: 1;
  flex-direction: column;
  align-content: center;
  max-width: 600px;
}
.form-call label{
  margin: 10px 10px 5px 10px;
  text-align: left;
}
.form-call input{
  margin: 10px;
  border: 0.1px;
  border-radius: 30px;
  padding: 12px;
  font-size: 18px;
  box-shadow: 0 10px 6px -6px #BDBDBD;
}
.form-call input[type=number]{
  margin-top: 5px;
}

.form-call input[type=submit]{
  background: #4CAF50;
  color: #ffff;
}

.form-call input[type=submit]:hover{
  background: #3ADF00;
  cursor: pointer;
}

</style>




